import React, {Component} from "react";
import Table from "react-bootstrap/Table";

class TableViewer extends Component {

    constructor(props) {
        super(props);
        this.state = {
            contentLoaded: false,
            tableContent: []
        }
    }

    componentDidMount() {
        fetch(`https://sql-tables.herokuapp.com/api/${this.props.match.params.tableName}`)
            .then(res => res.json())
            .then(data => {
                this.setState({
                    contentLoaded: true,
                    tableContent: data
                });
            });
    }

    render() {
        if(this.state.contentLoaded) {
            const columns = Object.getOwnPropertyNames(this.state.tableContent[0]);
            const columnNames = columns.map(col => <th>{col}</th>);  // Head row

            const contentRows = this.state.tableContent.map(row => {    // All content rows
                const rows = Object.values(row).map(cell => <td>{cell}</td>)    // Cells for each content row
                return <tr key={row.id} >{rows}</tr>;
            });

            return (
                <div>
                    <Table striped bordered hover>
                        <caption style={{captionSide: "top"}}>{this.state.selectedTable}</caption>
                        <thead>
                        <tr>
                            {columnNames}
                        </tr>
                        </thead>
                        <tbody>
                        {contentRows}
                        </tbody>
                    </Table>
                </div>
            );
        } else {
            return <div>Loading content...</div>
        }
    }

}

export default TableViewer;